.nav {
  background-color: $main-color;
  position: relative;
}

.nav__list {
  display: flex;
  flex-direction: column;
}

.nav__item {
  height: $menu-item-height;
  line-height: $menu-item-height;
  border-bottom: 1px solid rgba(51, 51, 51, 0.1);
  border-top: 1px solid rgba(248, 248, 248, 0.2);

  &--logo {
    box-shadow: 0 -3px 5px rgba(51, 51, 51, 0.1);
    border-bottom: none;
    border-top: none;
    background-color: #ffffff;
  }

  &:active {
    background-color: $down-color-bg;
  }

  &:active .nav__link {
    color: $font-color-down;
  }
}

.nav__link {
  text-decoration: none;
  color: #ffffff;
  display: block;
  width: 100%;
  height: 100%;
}

.nav__active {
  background-color: $hover-color;
}

@media (max-width: $mobile-width-only) {
  .nav__toggle-button {
    position: absolute;
    border: none;
    outline: none;
    background: url("../img/icon-cross.svg") no-repeat;
    background-size: contain;
    width: 24px;
    height: 24px;
    right: 20px;
    top: 15px;
    font-size: 0;
  }

  .nav--closed .nav__toggle-button {
    background: url("../img/icon-menu.svg") no-repeat;
    outline: none;
  }

  .nav--closed .nav__item {
    display: none;
  }

  .nav--closed .nav__item--logo {
    display: block;
  }

  .nav--nojs .nav__toggle-button {
    display: none;
  }

  .nav--nojs .nav__item {
    display: block;
  }
}

@media (min-width: $tablet-width) {
  .nav {
    background-color: #ffffff;
  }

  .nav__toggle-button {
    display: none;
  }

  .nav--closed .nav__item {
    display: block;
  }

  .nav__list {
    flex-direction: row;
    justify-content: space-around;
  }

  .nav__item {
    min-width: 145px;
    border-bottom: none;
    border-top: none;

    &--pos1 {
      order: -3;
    }

    &--pos2 {
      order: -2;
    }

    &--logo {
      order: -1;
      position: static;
      box-shadow: none;
    }

    &:active {
      background-color: #ffffff;
    }

    &:active .nav__link {
      color: $color-down;
    }
  }

  .nav__link {
    color: black;
  }

  .nav__link:hover {
    color: $main-color;
  }

  .nav__active .nav__link:hover {
    color: $font-menu-active-hover;
  }

  .nav__active {
    background-color: #ffffff;

    &:active .nav__link {
      color: $color-down;
    }
  }

  .nav__active .nav__link {
    color: $font-menu-active;
  }

  .nav__active .nav__link:active {
    color: $font-menu-active-down;
  }
}

@media (min-width: $desktop-width) {
  .nav__item {
    min-width: 202px;
  }
}
